.builderTable {
  padding-bottom: 15px;

  .builderRow:first-child .indentCol {
    visibility: hidden;
  }

  & > div:nth-child(odd) {
    background-color: #f2f2f2;
  }
}

.builderRow {
  display: flex;

  div {
    flex: 1;
    padding: 4px 2px 1px;
  }

  .colDropdown {
    flex: 0 0 auto;
    margin-right: 20px;
  }

  .indentCol {
    flex: 0 0 auto;
    margin-right: 20px;
  }

  .formatCol {
    flex: 1;
    input {
      width: 100%;
    }
    margin-right: 20px;
  }

  .rowSettings {
    flex: 0 0 30px;
    cursor: pointer;
    color: #3a3a3a;

    &:hover {
      color: #000000;
    }
  }

  .deleteRow {
    flex: 0 0 30px;
    cursor: pointer;
    color: #3a3a3a;

    &:hover {
      color: #990000;
    }
  }
}

.invalidRow {
  font-style: italic;
  color: #990000;
}

.builderSettings > div {
  display: flex;
  line-height: 30px;

  .settingsCol1 {
    flex: 0 0 150px;
  }

  label {
    margin-right: 10px;
  }
}

.tip {
  color: #999999;
}

.errorField,
.errorField:focus {
  border: 1px solid #cc0000;
  outline: none;
}
